const tableColumns = [
  {
    title: 'Name',
    field: 'nickName',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Created At',
    field: 'createdAt',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Country',
    field: 'country',
    isIcon: false,
    icon: ''
  }
]
const types = [
  {
    name: 'objectId',
    value: 'objectId',
    method: 'remoteSearch',
    labelType: 'title'
  },
  {
    name: 'userId',
    value: 'userId',
    method: 'userSearch',
    labelType: 'nickName'
  }
]

const tableActions = [
  {
    tag: 'link',
    class: 'btn btn-info',
    label: 'Show info',
    dynamic: false,
    to: '/users/:id',
    urlType: 'user_id',
    emit: ''
  }
]

export {
  tableColumns,
  tableActions,
  types
}



