<template>
  <Container>
    <form>
      <div class="row">
        <div class="col-md-12">
          <div>
            <label class="label-role">Select a role</label>
            <el-radio
              v-for="type in ['manager', 'owner']"
              :key="type"
              name="controlType"
              v-model="controlType"
              @change="value =>
                handleChoose({
                  key: 'controlType',
                  value
                })"
              :label="type"
            >
              {{ type.toUpperCase() }}
            </el-radio>
          </div>
          <hr >
          <div>
            <label class="label-role">Select control object</label>
            <el-radio
              v-for="(label, index) in ['event', 'location']"
              :key="index"
              name="type"
              v-model="type"
              @change="value =>
                handleChoose({
                  key: 'type',
                  value
                })"
              :label="label"
            >
              {{ label.toUpperCase() }}
            </el-radio>
          </div>
          <hr />
        </div>
        <div class="col-md-12">
          <div class="content-box">
            <label class="label-role">Found control object</label>
            <SelectSearch
              :type="type"
              :options="foundResults"
              labelType="title"
              name="objectId"
              :initialValue="form.objectId"
              @handleSearch="remoteSearch"
              @handleChange="handleSetControl"
            />
          </div>
          <hr>
          <div class="content-box">
            <label class="label-role">Found and choose user</label>
            <SelectSearch
              :options="userSearchResults"
              labelType="nickName"
              type="user"
              name="userId"
              :initialValue="form.userId"
              @handleSearch="userSearch"
              @handleChange="handleSetControl"
            />
          </div>
        </div>
      </div>
      <hr>
      <ButtonBlock
        backUrl="/users/list"
        @submit="submitControlForm"
      >
        {{id ? 'Update' : 'Create'}}
      </ButtonBlock>
    </form>
  </Container>
</template>

<script>
  import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
  import { types } from './TableMock'

  export default {
    components: {
      ButtonBlock: () => import('ui/Container/ButtonBlock'),
      Container: () => import('ui/Container/Container')
    },
    data: () => ({
      type: 'event',
      controlType: 'manager',
      loading: false,
      id: '',
      types
    }),
    computed: {
      ...mapState({
        form: ({ permissions }) => permissions.controlForm
      }),
      ...mapGetters([
        'foundResults',
        'userSearchResults'
      ])
    },
    mounted() {
      this.id = this.$route.params.id
      if (this.id) {
        this.fetchRole({ id: this.id })
      } else {
        this.clearRoleSearch()
      }
    },
    methods: {
      submitForm () {
        const id = this.id ? this.id : ''
        id ? this.updateControl({ id }) : this.createControl()
        this.$router.push('/users/list')
      },
      handleChoose({ value, key }) {
        this.clearRoleSearch()
        // this.$children[6].query = ''
        this.updateControlForm({ value, key })
      },
      handleSetControl(value, key) {
        this.updateControlForm({ key, value })
      },
      ...mapMutations([
        'updateControlForm',
        'createControl',
        'clearRoleSearch'
      ]),
      ...mapActions([
        'submitControlForm',
        'remoteSearch',
        'userSearch',
        'fetchRole'
      ])
    }
  }
</script>

<style>
  .label-role {
    display: block;
  }
</style>



